	export default {
		data() {
			return {
			  api: this.GLOBAL.baseURL,
				changeForm:{
					oldpass:"",
					newpass:"",
					checkpass:""
				},
				telForm:{
          m_code:'',
					tel:"",
					checkcode:"",
					newpass:"",
					checkpass:""
				},
        mobileForm:{
			    smsCode:"",
          sms:'发送验证码',
          password:'',
          rePassword:"",
          forbid:0
        },
				emailForm:{
          smsCode:'',
          sms:'发送邮箱验证码',
          password:"",
          rePassword:'',
          forbid:0,
				},
        pass:{
          old_password:'',
			    password: '',
          re_password:''
        },
        rePassword:{
			    m_code:'',
          tel:"",
          sms:'发送验证码',
          fobid:0,
          checkcode:'',
          password:'',
          re_password:''
        },
        phoneChick:{
          m_code:'',
          tel:'',
          sms_code:'',
          forbid:0,
          newTel:'',
          sms:'发送验证码',
          newm_code:'',
          newsms_code:"",
          newforbid:'',
          newsms:'发送验证码'
        },
        emailChick:{
          emailUrl:'',
          sms_code:'',
          forbid:'',
          newEmail:'',
          newSms_code:'',
          newSms:'发送邮箱验证码',
          sms:'发送邮箱验证码'
        },
        saveForm:{
			    newPass:'',
          rePass:''
        },
				activeNames:[],
        countryNumber:[
          {value:1,label:'+86'}
        ],
        changeTime:'',
        overChange:'',
        smsTime:60,
        overTime: 60,
        passwordR:[],
        isPassword:false,
        Account:[],
        mobile_link:'',
        mobile:'',
        mobileCode:'',
        email_link:'',
        email:'',
        isSecurity:false,
        sms:'发送验证码',
        forbid:1,
        smsCode:'',
        loginMobile:'',
        loginm_code:'',
        loginEmail:'',
        type:1
			};
		},
		methods:{
		  //找回安全密码
      returnSave(){
        if(!this.mobile){
          this.$message({
            type: 'info',
            message: '请绑定手机号'
          })
        }else{
          this.$http.post(this.api + '/home/User/send_msg',{
            token: localStorage.getItem('token'),
            m_code: parseInt(this.mobileCode),
            mobile: (this.mobile),
            flag:6
          })
            .then(res=>{
              if(res.data.code == 1){
                this.rePassword.fobid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime--
                  this.rePassword.sms = '已发送（'+this.smsTime+ '）'
                  if(this.smsTime <= 0){
                    this.smsTime = 60
                    this.rePassword.fobid = 0
                    this.rePassword.sms = '重新发送'
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.returnSave()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }
      },
      //换绑邮箱发送验证码
      newSendEmail(){
        this.$http.post(this.api + '/email',{
          token: localStorage.getItem('token'),
          toemail: this.emailChick.newEmail,
          flag: 4
        })
          .then(res=>{
            if(res.data.code == 1){
              this.emailChick.forbid = 1
              this.changeTime = setInterval(()=>{
                this.overTime--
                this.emailChick.newSms = '已发送（'+this.overTime+ '）'
                if(this.overTime <= 0){
                  this.overTime = 60
                  this.emailChick.forbid = 0
                  this.emailChick.newSms = '重新发送'
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.newSendEmail()
                })
            }else if(res.data.code == 0){
              this.$alert(res.data.msg)
            }
          })
      },
		  //手机登录发送验证码
      sendMobile(){
        if(!this.loginMobile){
          this.$message({
            type: 'info',
            message: '你不是手机登录的哟！'
          })
        }else{
          this.$http.post(this.api + '/home/User/send_msg',{
            token: localStorage.getItem('token'),
            m_code: this.mobileCode,
            mobile: this.mobile,
            flag: 1
          })
            .then(res=>{
              if(res.data.code == 1){
                this.mobileForm.forbid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime --
                  this.mobileForm.sms = '已发送('+this.smsTime+'）'
                  if(this.smsTime == 0){
                    this.smsTime = 60
                    this.mobileForm.forbid =  0
                    this.mobileForm.sms = '重新发送'
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.sendMobile()
                  })
              }
            })
        }
      },
		  //手机登录修改密码
      checkMobile(){
        this.$http.post(this.api + '/FindMobilePwd',{
          token: localStorage.getItem('token'),
          m_code: this.loginm_code,
          mobile: this.loginMobile,
          sms_code: this.mobileForm.smsCode,
          password: this.mobileForm.password,
          re_password: this.mobileForm.rePassword
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '修改成功！'
              })
              localStorage.removeItem('isLogin')
              localStorage.removeItem('userImg')
              localStorage.removeItem('token')
              this.$router.push('/')
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.checkMobile()
                })
            }
          })
      },
		  //邮箱登陆修改密码发送验证码
      sendEmailFor(){
        if(!this.loginEmail){
          this.$message({
            type: 'info',
            message: '您不是邮箱登陆!'
          })
        }else{
          this.$http.post(this.api + '/email',{
            token: localStorage.getItem('token'),
            toemail: this.email,
            flag:2
          })
            .then(res=>{
              if(res.data.code == 1){
                this.emailForm.forbid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime --
                  this.emailForm.sms = '已发送('+this.smsTime+')'
                  if( this.smsTime == 0){
                    this.smsTime = 60
                    this.emailForm.forbid = 0
                    this.emailForm.sms = '重新发送'
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.sendEmailFor()
                  })
              }else{

              }
            })
        }
      },
      //邮箱登陆修改密码
      checkEmail(){
        this.$http.post(this.api + '/FindLoginPwd',{
          token: localStorage.getItem('token'),
          email: this.loginEmail,
          sms_code: this.emailForm.smsCode,
          password: this.emailForm.password,
          re_password: this.emailForm.rePassword
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type:'success',
                message: '修改成功!'
              })
              localStorage.removeItem('isLogin')
              localStorage.removeItem('userImg')
              localStorage.removeItem('token')
              this.$router.push('/')
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.checkEmail()
                })
            }
          })
      },
      sendEmail(){
        this.$http.post(this.api + '/email',{
          token: localStorage.getItem('token'),
          toemail: this.email_link == 1? this.email:this.emailChick.emailUrl,
          flag: this.email_link == 1? 3:4
        })
          .then(res=>{
            if(res.data.code == 1){
              this.emailChick.forbid = 1
              this.changeTime = setInterval(()=>{
                this.smsTime--
                this.emailChick.sms = '已发送（'+this.smsTime+ '）'
                if(this.smsTime <= 0){
                  this.smsTime = 60
                  this.emailChick.forbid = 0
                  this.emailChick.sms = '重新发送'
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.sendEmail()
                })
            }else if(res.data.code == 0){
              this.$alert(res.data.msg)
            }
          })
      },
      mekdeEmail(){
        if(this.email_link == 1){
          if(!this.emailChick.sms_code){
            this.$message({
              type: 'info',
              message: '请输入验证码！'
            })
          }else if(!this.emailChick.newEmail){
            this.$message({
              type: 'info',
              message: '请输入新邮箱！'
            })
          }else if(!this.emailChick.newSms_code){
            this.$message({
              type: 'info',
              message: '请输入新邮箱验证码!'
            })
          }else{
            this.$http.post(this.api + '/BindEditEmail',{
              token: localStorage.getItem('token'),
              email_original: this.email,
              sms_code_original: this.emailChick.sms_code,
              email: this.emailChick.newEmail,
              sms_code: this.emailChick.newSms_code
            })
              .then(res=>{
                if(res.data.code == 1){
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                  this.$message({
                    type: 'success',
                    message: '绑定邮箱成功！'
                  })
                  this.emailChick = {
                    newEmail:'',
                    newSms_code:'',
                    sms_code:''
                  }
                  this.Account=[]
                  this.getUser()
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.mekdeEmail()
                    })
                }else if(res.data.code == 0){
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                  alert(res.data.msg)
                }
              })
          }
        }else{
         if(!this.emailChick.emailUrl){
            this.$message({
              type: 'info',
              message: '请输入邮箱地址！'
            })
          }else  if(!this.emailChick.sms_code){
           this.$message({
             type: 'info',
             message: '请输入验证码！'
           })
         }else{
           this.$http.post(this.api + '/BindEmail',{
             token: localStorage.getItem('token'),
             email: this.emailChick.emailUrl,
             sms_code: this.emailChick.sms_code
           })
             .then(res=>{
               if(res.data.code == 1){
                 clearInterval(this.changeTime)
                 clearInterval(this.overChange)
                 this.smsTime = '60'
                 this.overTime = '60'
                 this.$message({
                   type: 'success',
                   message: '绑定邮箱成功！'
                 })
                 this.Account=[]
                 this.getUser()
               }else if(res.data.code == 3){
                 this.$http.post(this.api + '/home/index/token')
                   .then(res=>{
                     localStorage.setItem('token',res.data.data)
                     this.mekdeEmail()
                   })
               }else if(res.data.code == 0){
                 clearInterval(this.changeTime)
                 clearInterval(this.overChange)
                 this.smsTime = '60'
                 this.overTime = '60'
                 this.$alert(res.data.msg)
               }
             })
         }
        }
      },
      //设置安全验证验证码
      takeSave(){
        if(this.mobile){
          this.$http.post(this.api + '/home/User/send_msg',{
            token: localStorage.getItem('token'),
            m_code: parseInt(this.mobileCode),
            mobile: this.mobile,
            flag: 6
          })
            .then(res=>{
              if(res.data.code == 1){
                this.forbid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime--
                  this.sms = '已发送（'+this.smsTime+ '）'
                  if(this.smsTime <= 0){
                    this.smsTime = 60
                    this.forbid = 0
                    this.sms = '重新发送'
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.takeSave()
                  })
              }
            })
        }else{
          this.$http.post(this.api + '/email',{
            token: localStorage.getItem('token'),
            toemail: this.email,
            flag: 5,
          })
            .then(res=>{
              if(res.data.code == 1){
                this.forbid = 1
                this.changeTime = setInterval(()=>{
                  this.smsTime--
                  this.sms = '已发送（'+this.smsTime+ '）'
                  if(this.smsTime <= 0){
                    this.smsTime = 60
                    this.forbid = 0
                    this.sms = '重新发送'
                    clearInterval(this.changeTime)
                  }
                },1000)
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.takeSave()
                  })
              }else if(res.data.code == 0){
                this.$alert(res.data.msg)
              }
            })
        }
      },
      handleFirstChange(val){
        if(val.length){
          if(this.mobile_link||this.email_link){
            this.$confirm('你正在设置账户安全密码, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
                this.isSecurity = true
                this.takeSave()
            }).catch(() => {
              this.isSecurity = false
              this.passwordR = []
            });
          }else{
            this.passwordR = []
            this.$message({
              type: 'info',
              message: '你还没有绑定手机号或者邮箱哦！'
            })
          }
        }
      },
      abolishSecur(){
        this.isSecurity = false
        this.passwordR = []
      },
      handleChange(val){

      },
      changePassword(val){
        if(val == 1){
          if(this.mobile_link){
            if(!this.phoneChick.sms_code){
              this.$message({
                type: 'info',
                message: '请输入验证码！'
              })
            }else if(!this.phoneChick.newTel){
              this.$message({
                type: 'info',
                message: '请输入新手机号码！'
              })
            }else if(!this.phoneChick.newm_code){
              this.$message({
                type: 'info',
                message: '请输入区号!'
              })
            }else if(!this.phoneChick.newsms_code){
              this.$message({
                type: 'info',
                message: '请输入新手机号的验证码!'
              })
            }else{
              this.$http.post(this.api + '/BindEditMobile',{
                token: localStorage.getItem('token'),
                m_code_original: parseInt(this.mobileCode),
                mobile_original: this.mobile,
                sms_code_original: this.phoneChick.sms_code,
                m_code:parseInt(this.phoneChick.newm_code) ,
                mobile: this.phoneChick.newTel,
                sms_code: this.phoneChick.newsms_code
              })
                .then(res=>{
                  if(res.data.code == 1){
                    this.$message({
                      type: 'success',
                      message: '换绑成功！'
                    })
                    this.phoneChick = {
                      sms_code:'',
                      newTel:'',
                      newm_code:'',
                      newsms_code:"",
                      newforbid:'',
                    }
                    clearInterval(this.changeTime)
                    clearInterval(this.overChange)
                    this.smsTime = '60'
                    this.overTime = '60'
                    this.Account=[]
                    this.getUser()
                  }else if(res.data.code == 3){
                    this.$http.post(this.api + '/home/index/token')
                      .then(res=>{
                        localStorage.setItem('token',res.data.data)
                        this.changePassword(val)
                      })
                  }else if(res.data.code == 0){
                    alert(res.data.msg)
                    clearInterval(this.changeTime)
                    clearInterval(this.overChange)
                    this.smsTime = '60'
                    this.overTime = '60'
                  }
                })
            }
          }else{
            this.$http.post(this.api + '/BindMobile',{
              token: localStorage.getItem('token'),
              m_code: parseInt(this.phoneChick.m_code),
              mobile: this.phoneChick.tel,
              sms_code: this.phoneChick.sms_code
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.$message({
                    type: 'success',
                    message: '绑定成功！'
                  })
                  this.phoneChick = {
                    sms_code:'',
                    newTel:'',
                    newm_code:'',
                    newsms_code:"",
                    newforbid:'',
                  }
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                  this.Account=[]
                  this.getUser()
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.changePassword(val)
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                }
              })
          }
        }else if(val == 3){
          if(this.saveForm.newPass.length  != 6 || !(/^[0-9]+$/).test(this.saveForm.newPass) ){
            this.$message({type:'error',message:'请输入6位数密码'})
          }else if(this.saveForm.rePass.length != 6 || !(/^[0-9]+$/).test(this.saveForm.rePass) ){
            this.$message({type: 'error',message: '重复密码输入错误'})
          }else{
            this.$http.post(this.api + '/PaywordSet',{
              token: localStorage.getItem('token'),
              password: this.saveForm.newPass,
              re_password: this.saveForm.rePass
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isPassword = true
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                  this.$message({
                    type:'success',
                    message:'设置成功!'
                  })
                }else if(res.data.code == 3){
                  this.changePassword(val)
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                  clearInterval(this.changeTime)
                  clearInterval(this.overChange)
                  this.smsTime = '60'
                  this.overTime = '60'
                }
              })
          }
        }else{
          this.$http.post(this.api + '/home/User/edit_pwd',{
            token: localStorage.getItem('token'),
            ori_password: this.changeForm.oldpass,
            password: this.changeForm.newpass,
            re_password: this.changeForm.checkpass,
          })
            .then(res=>{
              if(res.data.code == 1){
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
                localStorage.removeItem('isLogin')
                this.$router.push('/')
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.changePassword(val)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
              }
            })
        }
      },
      //修改安全密码
      check(){
        if(this.pass.old_password.length  != 6 || !(/^[0-9]+$/).test(this.pass.old_password) ){
          this.$message({type:'error',message:'请输入6位数数字密码'})
        }else if(this.pass.old_password.length  != 6 || !(/^[0-9]+$/).test(this.pass.password) ){
          this.$message({type:'error',message:'请输入6位数数字密码'})
        }else if(this.pass.old_password.length != 6 || !(/^[0-9]+$/).test(this.pass.re_password)) {
          this.$message({type:'error',message:'请输入6位数数字密码'})
        }else{
          this.$http.post(this.api + '/PaywordEdit',{
            token: localStorage.getItem('token'),
            ori_password: this.pass.old_password,
            password: this.pass.password,
            re_password:this.pass.re_password
          })
            .then(res=>{
              if(res.data.code == 1){
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
                this.$message({
                  type: 'info',
                  message: '安全密码修改成功!'
                })
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.check()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
              }
            })
        }
      },
      sendSms(val){
        if(this.mobile_link){
          if(val == 1){
            this.$http.post(this.api + '/home/User/send_msg',{
              token: localStorage.getItem('token'),
              m_code: parseInt(this.mobileCode),
              mobile: (this.mobile),
              flag:4
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.phoneChick.forbid = 1
                  this.changeTime = setInterval(()=>{
                    this.smsTime--
                    this.phoneChick.sms = '已发送（'+this.smsTime+ '）'
                    if(this.smsTime == 0){
                      this.smsTime = 60
                      this.phoneChick.forbid = 0
                      this.phoneChick.sms = '重新发送'
                      clearInterval(this.changeTime)
                    }
                  },1000)
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      if(res.data.code == 1){
                        localStorage.setItem('token',res.data.data)
                        this.onloginSmS(val)
                      }
                    })
                }else if(res.data.code == 0){
                  this.$alert(res.data.msg)
                }
              })
          }else{
            this.$http.post(this.api + '/home/User/send_msg',{
              token: localStorage.getItem('token'),
              m_code: parseInt(this.phoneChick.newm_code),
              mobile: this.phoneChick.newTel,
              flag:5
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.phoneChick.newforbid = 1
                  this.overChange = setInterval(()=>{
                    this.overTime--
                    this.phoneChick.newsms = '已发送（'+this.overTime+ '）'
                    if(this.overTime == 0){
                      this.overTime = 60
                      this.phoneChick.newforbid = 0
                      this.phoneChick.newsms = '重新发送'
                      clearInterval(this.overChange)
                    }
                  },1000)
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      if(res.data.code == 1){
                        localStorage.setItem('token',res.data.data)
                        this.onloginSmS(val)
                      }
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        }else{
            this.$http.post(this.api + '/home/User/send_msg',{
              token: localStorage.getItem('token'),
              m_code: parseInt(this.phoneChick.m_code),
              mobile: this.phoneChick.tel,
              flag:5
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.phoneChick.forbid = 1
                  this.changeTime = setInterval(()=>{
                    this.smsTime--
                    this.phoneChick.sms = '已发送（'+this.smsTime+ '）'
                    if(this.smsTime <= 0){
                      this.smsTime = 60
                      this.phoneChick.forbid = 0
                      this.phoneChick.sms = '重新发送'
                      clearInterval(this.changeTime)
                    }
                  },1000)
                }else if(res.data.code == 3){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      if(res.data.code == 1){
                        localStorage.setItem('token',res.data.data)
                        this.onloginSmS(val)
                      }
                    })
                }else if(res.data.code == 0){
                  this.$alert(res.data.msg)
                }
              })
        }
      },
      loadMima(){
        if(this.rePassword.password.length  != 6 || !(/^[0-9]+$/).test(this.rePassword.password) ){
          this.$message({type:'error',message:'请输入6位数数字密码'})
        }else if(this.rePassword.re_password.length != 6 || !(/^[0-9]+$/).test(this.rePassword.re_password) ){
          this.$message({type:'error',message:'请输入6位数数字密码'})
        }else{
          this.$http.post(this.api + '/PaywordEditCode',{
            token: localStorage.getItem('token'),
            m_code: this.mobileCode,
            mobile: this.mobile,
            sms_code: this.rePassword.checkcode,
            password: this.rePassword.password,
            re_password: this.rePassword.re_password
          })
            .then(res=>{
              if(res.data.code == 1){
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
                this.rePassword = {
                  checkcode:'',
                  password:'',
                  re_password:'',
                  sms:'发送验证码',
                  fobid:0,
                }
                this.$message({
                  type: 'success',
                  message:'找回成功！快去旅行吧!'
                })
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.loadMima()
                  })
              }else if(res.data.code == 0){
                this.$alert(res.data.msg)
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
              }
            })
        }
      },
      getUser(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data[0]
              if(data.mobile_link){
                this.mobile_link = 1
                this.mobile = data.mobile_link
                this.mobileCode = data.m_code_link
              }else{
                this.mobile_link = 0
                this.mobileCode = ''
                this.mobile = ''
              }
              if(data.email_link){
                this.email_link = 1
                this.email = data.email_link
              }else{
                this.email_link = 0
                this.email = ''
              }
              if(data.mobile){
                this.loginMobile = data.mobile
                this.loginm_code = data.m_code
              }
              if(data.email){
                this.loginEmail = data.email
              }
              if(data.is_setpwd){
                this.isPassword = true
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getUser()
                })
            }else if(res.data.code == 0){
              this.$alert(res.data.msg)
            }
          })
      },
      //安全验证
      checkSave(){
        if(this.mobile){
          this.$http.post(this.api + '/VeriMobileCode',{
            token: localStorage.getItem('token'),
            m_code: this.mobileCode,
            mobile: this.mobile,
            sms_code: this.smsCode
          })
            .then(res=>{
              if(res.data.code == 1){
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
                this.$message({
                  type: 'success',
                  message: '验证成功！'
                })
                this.isSecurity = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.checkSave()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
              }
            })
        }else{
          this.$http.post(this.api + '/VeriEmailCode',{
            token: localStorage.getItem('token'),
            email: this.email,
            sms_code: this.smsCode
          })
            .then(res=>{
              if(res.data.code == 1){
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
                this.$message({
                  type: 'success',
                  message: '验证成功！'
                })
                this.isSecurity = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.checkSave()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                clearInterval(this.changeTime)
                clearInterval(this.overChange)
                this.smsTime = '60'
                this.overTime = '60'
              }
            })
        }
      }
		},
    mounted(){
		  this.getUser()
    },
    destroyed() {
		  localStorage.removeItem('rt')
    }
  }



